@charset "UTF-8";
/* リキッドレイアウト対応 */
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4414414414vw;
  }
}
@media (min-width: 1110px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.l-inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 1160px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.l-topNews {
  margin-top: 2.5rem;
}

.c-button {
  text-align: center;
  display: inline-block;
  font-size: 0.875rem;
  font-weight: 700;
  color: #fff;
  width: 12.5rem;
  text-transform: uppercase;
  background: transparent linear-gradient(90deg, #7633A4 0%, #250D65 100%) 0% 0% no-repeat padding-box;
  border-radius: 50px;
  line-height: 1;
  padding: 1.25rem 0;
}

.c-hamburger {
  position: relative;
  width: 1.875rem;
  height: 1.875rem;
  z-index: 100;
}

.c-hamburger span {
  position: absolute;
  width: inherit;
  height: 0.125rem;
  background-color: #fff;
  transition: 0.3s;
}

.c-hamburger span:nth-child(1) {
  top: 15%;
}

.c-hamburger span:nth-child(2) {
  top: 51%;
  opacity: 1;
}

.c-hamburger span:nth-child(3) {
  top: 85%;
}

.c-hamburger.is-active span:nth-child(1) {
  top: 60%;
  transform: rotate(45deg);
}

.c-hamburger.is-active span:nth-child(2) {
  top: 51%;
  opacity: 0;
}

.c-hamburger.is-active span:nth-child(3) {
  top: 60%;
  transform: rotate(-45deg);
}

.c-mv-btn {
  display: inline-block;
  padding: 1.1875rem 4.375rem;
  border: 1px solid #fff;
  line-height: 1;
  font-size: 0.9375rem;
  letter-spacing: 0.05em;
  font-family: "Montserrat", sans-serif;
  border-radius: 50px;
}

.c-section-header {
  color: #391E88;
  text-align: center;
}

.c-section-header__jatitle {
  font-size: 0.8125rem;
  letter-spacing: 0.05em;
  line-height: 1;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .c-section-header__jatitle {
    font-size: 0.875rem;
  }
}

.c-section-header__engtitle {
  font-size: 1.875rem;
  letter-spacing: 0.05em;
  line-height: 1.2333333333;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .c-section-header__engtitle {
    font-size: 2.25rem;
  }
}

.p-footer-nav__items {
  display: flex;
  flex-wrap: wrap;
  gap: 1.25rem;
}

.p-footer-nav__item a {
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: 0.06em;
}

.p-footer {
  background-color: #120E19;
  padding-top: 3.75rem;
  padding-bottom: 1.25rem;
}

.p-footer__inner {
  padding: 0 1.875rem;
  margin: auto;
}

.p-footer__copyright {
  margin-top: 1.875rem;
  text-align: center;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 0.625rem;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.05em;
}

.p-header {
  height: 4rem;
  background-color: transparent;
  z-index: 1000;
  position: fixed;
  width: 100%;
}

.p-header__inner {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-header__inner {
    padding: 0 1.875rem;
  }
}

@media screen and (min-width: 768px) {
  .p-header__drawer {
    display: none;
  }
}

.p-headedr__pc-nav {
  height: inherit;
  display: none;
}
@media screen and (min-width: 768px) {
  .p-headedr__pc-nav {
    display: block;
  }
}

.p-header__logoLink {
  color: #fff;
  font-size: 1.125rem;
  display: block;
}

.p-mv {
  position: relative;
  height: 100vh;
  background-image: url(../images/common/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.p-mv__header {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 1.875rem;
  color: #fff;
}

.p-mv__title {
  font-size: 2.5rem;
  font-weight: 700;
  font-family: "Montserrat", sans-serif;
  line-height: 1.1;
}

.p-mv__subtitle {
  font-size: 1rem;
  letter-spacing: 0.1em;
  line-height: 1.8125;
  margin-top: 1.0625rem;
}

.p-mv__btn {
  margin-top: 1.75rem;
}

.p-mv__scroll {
  font-size: 0.75rem;
  letter-spacing: 0.2em;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  color: #fff;
  display: inline-block;
  position: absolute;
  right: 0;
  bottom: 78px;
  transform: rotate(90deg);
}

.p-mv__scroll span {
  position: relative;
}

.p-mv__scroll span::after {
  position: absolute;
  content: "";
  width: 1px;
  height: 50px;
  background-color: #fff;
  transform: rotate(90deg);
  right: -35px;
  bottom: -17px;
}

.p-news-content__meta {
  display: flex;
  align-items: center;
  font-family: "Montserrat", sans-serif;
}

.p-news-content__date {
  color: #333;
  font-size: 0.9375rem;
  letter-spacing: 0.1em;
}

.p-news-content__category {
  font-size: 0.6875rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  border-radius: 50px;
  line-height: 1;
  padding: 0.125rem 1.3125rem;
  background-color: #E8E8E8;
  margin-left: 0.9375rem;
}

.p-news-content__text {
  font-size: 0.875rem;
}

.p-news__items {
  margin-top: 1.9375rem;
}

.p-news__item + .p-news__item {
  margin-top: 2.1875rem;
}

.pc-nav__items {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
}

.pc-nav__item,
.pc-nav__item a {
  height: inherit;
}

.pc-nav__item a {
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 0.875rem;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.05em;
  font-weight: 700;
  line-height: 1;
  padding-left: 0.8125rem;
  padding-right: 0.75rem;
}

.pc-nav__item:first-child a {
  padding-left: 0;
}

.pc-nav__item:last-child a {
  padding-right: 0;
}

.sp-nav {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: #642390;
}

.sp-nav.is-active {
  display: block;
}

.sp-nav__items {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 6.25rem 0;
  min-height: 100vh;
}

.sp-nav__item {
  width: 100%;
}

.sp-nav__item a {
  text-align: center;
  display: block;
  padding: 0.625rem 0;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 0.9375rem;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.05em;
}